@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
@import url(https://fonts.googleapis.com/css?family=Aref+Ruqaa);
body {
  margin: 0;
  padding: 0;
  /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family:  Roboto Slab, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header, .container-fluid{
  padding : 0!important;
  margin : 0!important;
}
.bg-custom{
  background:url(data:image/gif;base64,R0lGODlhAQBHALMAANDQ0Ojo6Ovr6+zs7Onp6erq6u/v7+3t7e7u7vDw8PLy8vHx8QAAAAAAAAAAAAAAACH5BAAAAAAALAAAAAABAEcAAAQe8MiDajXYpJ2W90oojspnLtyWWcg0vEJczEQdBEAEADs=) left top repeat-x;
  height : 71px;
}
.navbar-toggler{
  background-color : grey!important;
}
.navbar-nav .nav-item .nav-link{
  color : black!important;
  font-weight : 700;
  padding : 25px 55px!important;
  border : 1px solid #e6e6e6;
  font-family: 'Aref Ruqaa', serif;
}
.navbar-nav .nav-item .nav-link:hover{
    color : white!important;
}
.navbar-nav .nav-item:hover{
  background-color : black;
}
.orange{
  color : orange;
}
.head-text{
   padding : 1%!important;
}
.head-text .row{
  padding : 0!important;
  margin : 0!important;
}
.head-text .row h1{
  margin-left : 10%;
  -webkit-transform : scale(1.1, 1.5)!important;
          transform : scale(1.1, 1.5)!important;
}
.head-text .row h1 span {
   font-size : 0.65em!important;
   color : #8c8c8c;
}
.head-text .row .offset-md-4{
  padding-top : 2%;
}
.form-input{
  border : 1px solid #bfbfbf!important;
}
.fa-search{
  position: absolute;
  float : right;
  top : 50%;
  left : 180%;
  z-index  : 99;
}
@media screen and (max-width : 768px){
   .fa-search{
     left : 72%;
     top : 44%;
   }
   .navbar{
     z-index : 9999;
   }
   .navbar-toggler{
     z-index: 99999;
   }
   .navbar-collapse{
     background-color : orange;
     z-index : 100;
   }
   .head-text .row h1{
        margin-left : 0%;
        font-size : 1.5em;        
   }
}

.banner-head{
  position: absolute;
  background-image : url(/InteriorDesign/static/media/bg-top.da6c28ee.jpg);
  background-repeat : no-repeat;
  background-size : COVER;
  width : 98.7vw;
  height : 110vh;
  padding : 3%;
}
.banner-content .row{
  padding : 0!important;
  margin : 0!important;
}
.banner-content{
   padding : 1%;
   margin-top : 1%;
   width :  65vw;
   margin-left : 15%;
   height : auto;
   background-color: white;
}
.banner-content-second{
    padding : 1%;
    margin-top : 5%;
    width : 65vw;
    margin-left : 20%;
    height : auto;
}
.banner-content .main-img{
    padding-top : 4%;
    width  : 40vw;
    height : 46vh;
}
.banner-content .banner-image{
  width : 12vw;
  padding-top : 13%;
}
.active{
  opacity : 0.5;
}
.remove{
  opacity : 1;
}
.banner-text a, .banner-text a:hover{
  color : orange;
}

.banner-text h2{
  font-weight: 500;
  padding : 15%;
  color : orange;
  -webkit-transform: scale(1.5, 2.5);
          transform: scale(1.5, 2.5);
}
.banner-text p{
  text-align  : left;
  padding : 3%;
  font-size : 0.9em;
}
.banner-head-content h2{
  text-align : left;
  padding : 4%;
  color : white;
}
.banner-head-content .img-thumbnail{
  border : 6px solid white!important;
  border-radius: 0px;
}
.banner-head-content .banner-inner ul{
  display: inline-block;
  float : right;
  margin-top : 1%;
  margin-left: -11%;
  color : orange;
  text-align : left;
  font-weight : 500;
}
.banner-content-second{
  text-align : left;
  font-size : 0.9em;
}
.banner-content-second h6 b, .banner-content-second p a{
  color : orange;
  font-weight: 500;
}
.bold{
  color : orange;
}
.quotes::before{
    content : "\"";
}
.fa-quote-left{
  color : orange;
}
.fa-quote-left + p{
  margin-left : 5%;
  margin-top : -7%;
}
.footer p{
    text-align : left;
}
.footer h3{
  -webkit-transform: scale(1.6, 1.3);
          transform: scale(1.6, 1.3);
  font-size : 1.5em;
}
.footer h3 span{
  color : #808080;
}
sub{
  top : 0;
}
@media screen and (max-width : 768px){
  .banner-head{
    height : 217vh!important;
  }
  .banner-content {
      width : 84vw;
      margin-left : 5%;
  }
  .banner-content .main-img{
      width  : 100vw!important;
      height : 30vh;
  }
  .banner-content .banner-image{
    width : 150vw;
    padding-top : 13%;
  }
  .banner-head-content ul li{
      text-align : center;
      float : none;
      position: relative;
      left : -50%;
  }
  .footer{
    width : 80vw;
  }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

